.card{
    margin:2rem 2rem 2rem 0;
    padding: 1.5rem;
    width: 250px;
    box-shadow: 0 0 28px 4px black;
    transition:transform .3s ease;
    cursor:pointer;
    background-color: rgb(247, 203, 230)!important;
    line-height: 25px;
    letter-spacing: 1px;
}

.card:hover{
    transform: scale(1.1);
}

.card_title{
    font-weight: 800;
    font-size: 1.5rem;
}

.card_image{
    display:flex;
    justify-content: flex-end;
    
}

.card_image img{
    border-radius: 50%;
    border: 3px solid rgb(197, 130, 142);
}

